
"use client";

import { useState } from 'react';
import { useReactMediaRecorder } from 'react-media-recorder';
import { Button } from '@/components/ui/button';
import { Mic, Loader2, StopCircle } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { transcribeAudio } from '@/ai/flows/speech-to-text';

interface VoiceInputProps {
  onTranscript: (transcript: string) => void;
  language?: string;
  className?: string;
}

const fileToDataUri = (blob: Blob): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => {
        if (typeof reader.result === 'string') {
          resolve(reader.result);
        } else {
          reject(new Error('Failed to convert blob to Data URI'));
        }
      };
      reader.onerror = reject;
      reader.readAsDataURL(blob);
    });
};

export function VoiceInput({ onTranscript, language = 'en-IN', className }: VoiceInputProps) {
  const [isTranscribing, setIsTranscribing] = useState(false);
  const { toast } = useToast();

  const handleAudioStop = async (blobUrl: string, blob: Blob) => {
    setIsTranscribing(true);
    try {
      const audioDataUri = await fileToDataUri(blob);
      
      const result = await transcribeAudio({ audioDataUri, languageCode: language });
      
      if (result.transcription) {
        onTranscript(result.transcription);
      } else {
        toast({
          variant: 'destructive',
          title: 'Transcription Failed',
          description: 'Could not understand audio. Please try again.',
        });
      }
    } catch (error) {
      console.error('Voice input error:', error);
      toast({
        variant: 'destructive',
        title: 'Voice Error',
        description: 'Failed to process audio. Please check permissions and try again.',
      });
    } finally {
      setIsTranscribing(false);
      clearBlobUrl();
    }
  };

  const {
    status,
    startRecording,
    stopRecording,
    clearBlobUrl,
  } = useReactMediaRecorder({ 
    audio: {
        channelCount: 1,
    },
    onStop: handleAudioStop,
    onPermissionDenied: () => {
        toast({
            variant: "destructive",
            title: "Microphone Permission Denied",
            description: "Please allow microphone access in your browser settings to use this feature."
        })
    }
  });

  return (
    <Button
      type="button"
      size="icon"
      className={`rounded-full ${className}`}
      onClick={status === 'recording' ? stopRecording : startRecording}
      disabled={isTranscribing}
    >
      {isTranscribing ? (
        <Loader2 className="animate-spin" />
      ) : status === 'recording' ? (
        <StopCircle className="text-destructive" />
      ) : (
        <Mic />
      )}
    </Button>
  );
};
