
"use client";

import React, { useState, useEffect, useRef } from 'react';
import { Button } from '@/components/ui/button';
import { Speaker, Loader2, StopCircle } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { synthesizeSpeech } from '@/ai/flows/text-to-speech';
import { cn } from '@/lib/utils';

interface VoiceOutputProps {
  text: string;
  language?: string;
  className?: string;
}

export function VoiceOutput({ text, language = 'en-IN', className }: VoiceOutputProps) {
  const [isPlaying, setIsPlaying] = useState(false);
  const [isGenerating, setIsGenerating] = useState(false);
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const { toast } = useToast();

  useEffect(() => {
    // Cleanup audio on component unmount
    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current = null;
      }
    };
  }, []);

  const playAudio = async () => {
    if (!text) return;
    
    // If already playing, stop it
    if (isPlaying && audioRef.current) {
        audioRef.current.pause();
        audioRef.current.currentTime = 0;
        setIsPlaying(false);
        return;
    }
    
    setIsGenerating(true);
    
    try {
      const result = await synthesizeSpeech({ text, languageCode: language });
      
      if (result.audioDataUri) {
        const audio = new Audio(result.audioDataUri);
        audioRef.current = audio;
        
        setIsPlaying(true);
        audio.play();
        
        audio.onended = () => {
          setIsPlaying(false);
          audioRef.current = null;
        };
        
        audio.onerror = () => {
          setIsPlaying(false);
          audioRef.current = null;
           toast({
            variant: 'destructive',
            title: 'Audio Error',
            description: 'Could not play the generated audio.',
          });
        };
      } else {
         toast({
            variant: 'destructive',
            title: 'Audio Generation Failed',
            description: 'The audio could not be generated for this text.',
         });
      }
    } catch (error) {
      console.error('TTS Error:', error);
       toast({
        variant: 'destructive',
        title: 'TTS Error',
        description: 'An unexpected error occurred while generating speech.',
      });
    } finally {
      setIsGenerating(false);
    }
  };

  return (
    <Button
      type="button"
      variant="ghost"
      size="icon"
      onClick={playAudio}
      disabled={isGenerating || !text}
      className={cn("rounded-full", className)}
    >
      {isGenerating ? (
        <Loader2 className="animate-spin" />
      ) : isPlaying ? (
        <StopCircle className="text-primary" />
      ) : (
        <Speaker />
      )}
    </Button>
  );
};
