
'use server';

/**
 * @fileOverview A Genkit flow for transcribing audio to text using Google Cloud Speech-to-Text API.
 * 
 * - transcribeAudio - A function that transcribes audio data.
 * - TranscribeAudioInput - The input type for the transcribeAudio function.
 * - TranscribeAudioOutput - The return type for the transcribeAudio function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'genkit';
import { SpeechClient } from '@google-cloud/speech';

const TranscribeAudioInputSchema = z.object({
  audioDataUri: z
    .string()
    .describe(
      "The audio data to transcribe, as a data URI. Expected format: 'data:audio/webm;codecs=opus;base64,<encoded_data>'."
    ),
  languageCode: z.string().optional().default('en-IN').describe('The language of the audio.'),
});
export type TranscribeAudioInput = z.infer<typeof TranscribeAudioInputSchema>;

const TranscribeAudioOutputSchema = z.object({
  transcription: z.string().describe('The transcribed text.'),
});
export type TranscribeAudioOutput = z.infer<typeof TranscribeAudioOutputSchema>;

// Initialize the SpeechClient.
// The client will automatically use the project's service account credentials
// when deployed on Firebase App Hosting. For local development, ensure you have
// authenticated with `gcloud auth application-default login`.
let speechClient: SpeechClient | undefined;
try {
  speechClient = new SpeechClient();
} catch (e) {
  console.warn("SpeechClient failed to initialize. Voice input may not work in local development if ADC is not configured. It will work when deployed.", e);
}


export async function transcribeAudio(
  input: TranscribeAudioInput
): Promise<TranscribeAudioOutput> {
  return transcribeAudioFlow(input);
}

const transcribeAudioFlow = ai.defineFlow(
  {
    name: 'transcribeAudioFlow',
    inputSchema: TranscribeAudioInputSchema,
    outputSchema: TranscribeAudioOutputSchema,
  },
  async ({ audioDataUri, languageCode }) => {
    if (!speechClient) {
        throw new Error("SpeechClient not initialized. This feature may only be available when deployed.");
    }
      
    // The base64 data is the part of the data URI after the comma
    const audioBytes = audioDataUri.split(',')[1];

    const audio = {
      content: audioBytes,
    };
    
    // Note: The 'chirp' model used here is part of the v2 API features,
    // but the Node.js client can access it via the v1p1beta1 endpoint.
    const config = {
      model: 'chirp',
      encoding: 'WEBM_OPUS' as const,
      sampleRateHertz: 48000, // This must match the client recording settings
      languageCode: languageCode,
    };

    const request = {
      audio: audio,
      config: config,
    };

    try {
      const [response] = await speechClient.recognize(request);
      const transcription =
        response.results
          ?.map((result) => result.alternatives?.[0].transcript)
          .join('\n') || '';

      return { transcription };
    } catch (error) {
        console.error('Speech-to-text API error:', error);
        throw new Error('Failed to transcribe audio.');
    }
  }
);
